.order-page {
  margin-top: 150px;
}

.orders-container {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
  border-radius: 8px;
  text-align: center;
}

.order-table-container {
  overflow-x: auto;
  margin-top: 20px;
  border-radius: 8px; 
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

.order-table {
  width: 100%;
  border-collapse: collapse;
}

.order-table th,
.order-table td {
  padding: 12px 15px;
  text-align: center;
  border-bottom: 1px solid #dddddd;
}

.order-table th {
  background-color: #f2f2f2;
}

.order-table tbody tr:nth-child(even) {
  background-color: #f9f9f9;
}

.order-table tbody tr:hover {
  background-color: #e9ecef;
}


@media(max-width: 1048px){
  .orders-container {
    width: 100%;
    max-width: 100%; 
    margin: 0 auto;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    background-color: #ffffff;
    border-radius: 8px;
    text-align: center;
    box-sizing: border-box; 
    overflow-x: auto;
  }
}