/* Base styles */
.x1y9q-container {
  max-width: 100%;
  width: 800px;
  margin: 0 auto;
  margin-top: 100px;
  padding: 20px;
  font-family: Arial, sans-serif;
  line-height: 1.6;
  box-sizing: border-box;
}

.a7b2e-title {
  font-size: 28px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 20px;
  text-decoration: underline;
}

.s3p8w-section {
  margin-bottom: 30px;
}

.k2p7f-heading {
  font-size: 22px;
  font-weight: bold;
  margin-bottom: 10px;
}

.t4r9y-paragraph {
  margin-bottom: 15px;
}

/* Responsive styles */
@media only screen and (max-width: 900px) {
  .x1y9q-container {
    max-width: 95%;
    padding: 15px;
  }

  .a7b2e-title {
    font-size: 26px;
  }

  .k2p7f-heading {
    font-size: 20px;
  }
}

@media only screen and (max-width: 600px) {
  .x1y9q-container {
    max-width: 100%;
    margin: 20px auto;
    padding: 10px;
  }

  .a7b2e-title {
    margin-top: 20%;
    font-size: 24px;
  }

  .k2p7f-heading {
    font-size: 18px;
  }

  .t4r9y-paragraph {
    margin-bottom: 10px;
  }
}
